'use strict';
import request from 'superagent'
import jsonp from 'superagent-jsonp'
import myInterface from '@/interface'


//vuex
const state = {
    list: [], //
    temp: [],
    step: 0, //
    detailItem: {} //
}
// Vuex 
const mutations = {
    getMoreData (state, payload) {
        state.step += 5;
        state.list = state.list.concat(payload.res);
    },
    getDetail (state, payload) {
        state.detailItem = payload.res
    }
}
//Vuex 
const actions = {
    /**
     * 
     * step: 5
     * count: 5
     */
    getActivityList ({commit,state}) {//es6contextcommitstate
        request
            .get(myInterface.getActivityList+'?loc='+myInterface.cityId+'&start=' + state.step + '&count='+myInterface.count)
            .use(jsonp)
            .end((err, res) => {
                if (!err) {
                    commit({
                        type: 'getMoreData',
                        res: res.body.events
                    });
                }
            })
    },
    /**
     * 
     * id: event id
     */
    getActivityDetail ({commit, state}, payload) {
        return new Promise((resolve, reject) => {
            request
                .get(myInterface.getActivityDetail + payload.activityId)//id
                .use(jsonp)
                .end((err, res) => {
                    if (!err) {
                        commit({
                            type: 'getDetail',
                            res: res.body
                        });
                        resolve(res.body);
                    }
                })
        })
    }
}

export default {
    state,
    mutations,
    actions
}




