<template>
    <div class="db-movie-rating" v-if="rating">
        <template v-if="fullStar === 0">
            <span class="db-movie-zero"></span>
        </template>
        <template v-else>
            <template v-for="n in fullStar">
                <span class="db-full-star"></span>
            </template>
            <template v-for="n in halfStar">
                <span class="db-half-star"></span>
            </template>
            <template v-for="n in grayStar">
                <span class="db-gray-star"></span>
            </template>
            <span class="db-movie-average">{{average}}</span>
        </template>
        <slot name="ratingsCount"></slot>
    </div>
    
</template>

<script type="text/javascript">
    export default {
        data() {
            return {
                average:0,//
                fullStar: 0,//
                halfStar: 0,//
                grayStar: 0//
            }
        },
        props: {
            rating: {
                type: Object,
                required: true
            }
        },
        created() {
            this.average = this.rating.average;//
            this.fullStar = parseInt(this.average / 2) //
            this.halfStar = this.average % 2 === 0 ? 0 : 1;//
            this.grayStar = 5 - this.fullStar - this.halfStar;//
        }
    }
</script>

<style lang="scss" scoped="">
    .db-movie-rating{
        margin-top: 0.5rem;
        span{
            display: inline-block;
            width: 1.3rem;
            height: 1.3rem;
            font-size: 1.4rem;
            color: #aaa;
            vertical-align: middle;
        }
        .db-full-star{
            background-image: url(data:image/png;base64,);
            background-size: 1.3rem 1.3rem;
        }
        .db-half-star {
            background-image: url(data:image/png;base64,);
            background-size: 1.3rem 1.3rem;
        }
        .db-gray-star {
            background-image: url(data:image/png;base64,);
            background-size: 1.3rem 1.3rem;
        }
        .db-movie-average {
            padding: 0.2rem 0.8rem 0 0;
            width: auto;
            height: auto;
        }
        .db-movie-zero{
            width: auto;
            height: auto;
        }
    }
</style>



