<template>
    <div id="app">
        <router-view @searchShow="getSearchShow"></router-view>
        <router-view name="centerPage"></router-view>
        <search v-show="searchShowType" @searchShow="getSearchShow"></search>
    </div>
</template>
<script>
    import search from "@/components/search"
    export default {
        name: 'app',
        data(){
          return {
              searchShowType:false
          }
        },
        components:{
            search
        },
        methods:{
            getSearchShow(data){
                this.searchShowType = data.showType;
            }
        }
    }
</script>

<style lang="scss">
    .slide-fade-enter-active {
        transition: all 3s ease;
    }
    .slide-fade-leave-active {
        transition: all 5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to{
        transform: translateX(10px);
        opacity: 0;
    }
    /*
     * CSS Reset 
     */
    html {
        /*
         * ()
         *  auto100%  none(bug)
         */
        font-size: 62.5%;
        -webkit-text-size-adjust: 100%;
        -ms-text-size-adjust: 100%;
    }

    body {
        /*
         * iOS 4.0+  Helvetica NeueiOS Helvetica
         * STHeiTi
         * iOS STHeiTi--
         * Android
         */
        font-family: "Helvetica Neue", Helvetica, STHeiTi, sans-serif;
    }

    body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input,
    textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer,
    header, hgroup, menu, nav, section {
        margin: 0;
        padding: 0;
        /*
         * AdnroidiOS
         */
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        -webkit-tap-highlight-color: transparent; /* */
        /*
         * ,
         */
        outline: none;
        font-weight: normal;
    }

    /*
     * 
     */
    .ban-show-menu {
        -webkit-touch-callout: none;
    }

    /*
    * 
    */
    .user-ban-select {
        user-select: none;
    }

    .user-can-select {
        user-select: auto;
    }

    /*
     * input number
     */
    input[type=number]::-webkit-outer-spin-button,
    input[type=number]::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }
    /*
     * -webkit-appearance
     */
    input, button, select, textarea, a {
        -webkit-appearance: none;
    }
    a, a:active {
        outline: none;
        text-decoration: none;
    }
    textarea {
        resize: none;
    }
    input, button, textarea, a {
        border: none;
    }
    .clearFix {
        zoom: 1;
        display: block;
        _height: 1px;
    }
    .clearFix:after {
        content: ".";
        display: block;
        height: 0;
        clear: both;
        visibility: hidden;
    }
    .clearFlow {
        overflow: hidden;
        *zoom: 1;
    }
    /*
     *  ul 
    **/
    ul li{
        list-style-type: none;
    }
    .ul-li-float li{
        float: left;
    }
    /*
     *  
    **/
    .border-box{
        box-sizing: border-box;
    }
    /*
     *  
    **/
    .content_box{
        box-sizing: content-box;
    }
    /*
     *  
    **/
    .flexible-box{
        display: flex;
    }
    .flexible_box_important{
        display: flex!important;
    }
    .flexible-box-h{
        display: flex;
        flex-direction: column;
    }
    .flexible-box_h-important{
        display: flex!important;
        flex-direction: column!important;
    }
    /*
     *  -
    **/
    .adaptive-box-flex{
        flex: 1;
    }
    .left{
        float:left;
    }
    .right{
        float:right;
    }
    #app{
        max-width: 41.2rem;
        margin: 0 auto;
    }
</style>



