'use strict';
import request from 'superagent'
import jsonp from 'superagent-jsonp'
import myInterface from '@/interface'


const state = {
    hotMovies: [],//
    topMovies: [],//
    newMovies: [],//
    movieTags: [ //
        {
            title: 'IMDB250250',
            href: 'https://m.douban.com/doulist/968362/',
            color: '#FFAC2D'
        },
        {
            title: '',
            href: 'https://m.douban.com/doulist/16002',
            color: '#FF4055'
        },
        {
            title: '',
            href: 'https://m.douban.com/doulist/190343',
            color: '#4F9DED'
        },
        {
            title: '',
            href: 'https://m.douban.com/doulist/1125971',
            color: '#FFC46C'
        },
        {
            line: true
        },
        {
            title: '',
            href: 'https://m.douban.com/doulist/4253902',
            color: '#2384E8'
        },
        {
            title: '',
            href: 'https://m.douban.com/doulist/121326',
            color: '#3BA94D'
        },
        {
            title: '2015',
            href: 'https://m.douban.com/doulist/37479562',
            color: '#42BD56'
        },
        {
            title: '100',
            href: 'https://m.douban.com/doulist/458087',
            color: '#CC3344'
        }
    ]
}

const mutations = {
    getMovie (state, payload) {
        switch (payload.tag) {
            case 'hotMovies':
                state.hotMovies = payload.res;
                break
            case 'newMovies':
                state.newMovies = payload.res;
                break
            case 'topMovies':
                state.topMovies = payload.res;
                break
            default:
                state.hotMovies = payload.res;
        }
    }
}

const actions = {
    /**
     * top250
     * count: 10
     */
    getMovieList ({ commit }) {
        request
            .get(myInterface.getShowMovieList+'?count=10&city=')
            .use(jsonp)
            .end((err, res) => {
                if (!err) {
                    commit({
                        type: 'getMovie',
                        tag: 'hotMovies',
                        res: res.body.subjects
                    })
                }
            })
        request
            .get(myInterface.getWillShowMovieList+'?count=10&city=')
            .use(jsonp)
            .end((err, res) => {
                if (!err) {
                    commit({
                        type: 'getMovie',
                        tag: 'newMovies',
                        res: res.body.subjects
                    })
                }
            })
        request
            .get(myInterface.getTop250MovieList+'?count=10')
            .use(jsonp)
            .end((err, res) => {
                if (!err) {
                    commit({
                        type: 'getMovie',
                        tag: 'topMovies',
                        res: res.body.subjects
                    })
                }
            })
    }
}

export default {
    state,
    mutations,
    actions
}




